.spacer {
	aspect-ratio: 700/100;
	width: 100%;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
}

.spacer-2 {
	aspect-ratio: 1100/300;
	width: 100%;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
}

.layer1 {
	background-image: url(../img/svg1.svg);
}
.layer2 {
	background-image: url(../img/svg2.svg);
	margin-top: -18rem;

	@include maxW(1279px) {
		margin-top: -13rem;
	}

	@include maxW(1023px) {
		margin-top: -10rem;
	}
}

.layer3 {
	background-image: url(../img/footer.svg);
}

.layer4 {
	background-image: url(../img/svg-1.png);
}

.svg-layer {
	width: 100%;
	display: block;
}
