.hero {
	padding: 8rem 0 8rem 17rem;
	background-image: linear-gradient(
		135deg,
		rgba($color-primary-light, 0.8),
		rgba($color-secondary-light, 0.5)
	);
	&__content {
		max-height: 60rem;
		display: grid;
		grid-template-columns: repeat(2, 1fr);
	}

	&__text-box {
		padding-top: 8rem;
		width: 100%;
	}
	display: block;

	@include maxW(1279px) {
		padding-left: 7rem;
	}

	@include maxW(767px) {
		padding: 8rem 3rem;
		&__content {
			grid-template-columns: 1fr;
		}

		&__img-box {
			display: none;
		}

		&__text-box {
			text-align: center;
		}
	}
}
