.gallery {
	padding-bottom: 9.8rem;
	&__main {
		max-width: 120rem;
		margin: 0 auto;
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		column-gap: 2rem;
	}

	.col {
		display: flex;
		flex-direction: column;
		gap: 2.3rem;

		&--2 {
			padding-top: 2rem;
		}
	}

	&__img-box {
		overflow: hidden;
		border-radius: 1.5rem;
		text-align: center;
		position: relative;

		&:hover .gallery__text {
			opacity: 1;
			left: 50%;
		}
	}
	&__text {
		display: inline-block;
		position: absolute;
		width: 80%;
		left: 100%;
		top: 45%;
		font-family: $font-secondary;
		font-size: 2.5rem;
		z-index: 10;
		color: white;
		transform: translate(-50%, -50%);
		opacity: 0;
		pointer-events: none;
		transition: all 0.6s ease-in-out;
	}

	&__img {
		border-radius: 1.5rem;
		filter: brightness(100%);
		transition: all 0.3s ease-in-out;
		position: relative;
		z-index: 1;
		&:hover {
			filter: brightness(40%);
			transform: scale(1.1);
			cursor: pointer;
		}
	}

	display: block;

	@include maxW(1280px) {
		&__main {
			max-width: 100rem;
		}
	}

	@include maxW(1023px) {
		&__main {
			max-width: 80rem;
		}
	}

	@include maxW(767px) {
		padding: 3rem;
		&__main {
			grid-template-columns: 1fr;
			row-gap: 6rem;
		}

		.col {
			gap: 4rem;
		}
	}
}
