.footer {
	margin-top: 1rem;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: bottom;
	display: grid;
	justify-items: center;
	align-items: center;
	// position: relative;

	p {
		// left: 30%;
		// top: 70%;
		// position: absolute;
		// z-index: 10;
		font-family: $font-secondary;
		text-transform: capitalize;
		font-size: 10rem;
		color: white;

		@include maxW(1023px) {
			font-size: 7rem;
		}

		@include maxW(767px) {
			font-size: 4rem;
		}
	}
}
