.story {
	padding: 0rem 0 9.8rem;

	&__main {
		display: grid;
		grid-template-columns: 1fr 1fr;
		gap: 9.8rem;
	}
	&__img-box {
		border-radius: 2rem;
		overflow: hidden;
	}

	&__img {
		border-radius: 2rem;
		transition: all 0.5s ease-in-out;
		&:hover {
			transform: scale(1.05);
			box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
		}
	}

	&__icons {
		margin-top: 2rem;
		display: flex;
		gap: 2rem;

		.face {
			fill: $color-primary;
			cursor: pointer;
		}

		.insta {
			fill: $color-secondary;
			cursor: pointer;
		}
	}

	&__icon {
		width: 4rem;
		height: 4rem;
	}

	display: block;

	@include maxW(767px) {
		&__main {
			grid-template-columns: 1fr;
		}

		.last {
			grid-row: 3;
		}
	}
}
