.heading-1 {
	font-size: 6.5rem;
	color: transparent;
	text-transform: uppercase;
	margin-bottom: 3rem;
	background-image: linear-gradient(45deg, $color-primary, $color-secondary);
	background-clip: text;
	-webkit-background-clip: text;

	@include maxW(767px) {
		font-size: 5rem;
	}
}

.heading-2 {
	color: transparent;
	text-transform: uppercase;
	font-size: 3.5rem;
	margin-bottom: 9.8rem;
	background-image: linear-gradient(to top, $color-primary, $color-secondary);
	background-clip: text;
	-webkit-background-clip: text;
}

.heading-3 {
	font-size: 1.8rem;
	color: transparent;
	text-transform: uppercase;
	margin-bottom: 2rem;
	background-image: linear-gradient(to top, $color-primary, $color-secondary);
	background-clip: text;
	-webkit-background-clip: text;
}

.caption {
	font-size: 2rem;
	text-transform: uppercase;
	letter-spacing: 1px;
	font-weight: 600;
	color: $color-primary;
	margin-bottom: 1rem;
}

.text {
	width: 100%;
	font-size: 2.2rem;
	margin-bottom: 5rem;
	line-height: 1.5;

	@media screen and (min-width: 767px) {
		width: 80%;
	}
}
