.photos {
	padding: 9.8rem 0;
	margin-bottom: 9rem;
	&__heading {
		display: inline;
		font-size: 3rem;
		text-transform: uppercase;
		font-weight: 600;
		color: transparent;
		background-image: linear-gradient(135deg, $color-primary, $color-secondary);
		background-clip: text;
		-webkit-background-clip: text;
	}

	&__main {
		padding: 20rem 16rem;
		margin-top: 3rem;
		background-image: url(../img/img8.jpg);
		background-size: cover;
		background-position: bottom;
		background-repeat: no-repeat;
		transform: skewY(-4deg);
		filter: brightness(100%);

		& > * {
			transform: skewY(4deg);
		}
	}

	&__list {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-template-rows: 1fr;
		gap: 5rem;
		justify-items: center;
		background-color: white;
		border-radius: 2rem;
		padding: 3rem 4rem;
	}

	&__item {
		cursor: pointer;
		border-radius: 1.5rem;
		overflow: hidden;

		&--front {
			width: 100%;
			position: relative;
		}
		&--slide {
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			right: -100%;
			background-image: linear-gradient(
				185deg,
				$color-secondary-lighter,
				$color-primary
			);
			transition: all 0.4s ease-in-out;
			padding: 5rem 3rem;
		}

		&:hover .photos__item--slide {
			right: 0;
		}
	}

	&__img {
		object-fit: cover;
		width: 100%;
	}

	&__caption {
		font-size: 2.3rem;
		font-weight: 600;
		padding: 1rem 2rem;
		color: $color-primary-dark;
		background-color: white;
		display: inline-block;
		border-radius: 1rem;
	}

	&__text {
		font-family: $font-secondary;
		font-size: 2.3rem;
		color: white;
		line-height: 1.3;
		text-align: justify;
	}

	display: block;

	@include maxW(1023px) {
		padding: 2rem 0;

		&__main {
			padding: 5rem 8rem;
		}

		&__caption {
			font-size: 1.8rem;
		}

		&__text {
			font-size: 1.5rem;
		}

		&__item--slide {
			padding: 3rem;
		}
	}

	@include maxW(767px) {
		padding: 7rem 0;

		&__list {
			grid-template-columns: 1fr;
		}
	}
}
